<template>
  <v-app id="inspire" class="inspire">
    <v-main>
      <v-container class="fill-height inspire" fluid>
        <v-row align="center" justify="center">
          <v-col cols="4" class="pr-0">
            <v-card flat tile class="gradiant pa-8 ml-8">
              <v-list-item-content class="white--text">
                <v-list-item-title class="headline mb-1"
                  >Create your future!</v-list-item-title
                >
                <p>
                  Got a lot of ideas, but don't have the right people beside
                  you? We got your back! Create your profile today and find the
                  right business partner for you.
                </p>
              </v-list-item-content>
            </v-card>
          </v-col>

          <v-col cols="6" class="pl-0">
            <v-card flat tile class="pa-8 mr-8">
              <v-container fluid>
                <v-form>
                  <v-text-field v-model="name" label="User Name"></v-text-field>

                  <!-- ===============================================================Starting the items table=============================================================== -->

                  <v-data-table
                    :headers="headers"
                    :items="desserts"
                    class="elevation-1 mb-8 yellow lighten-5"
                    hide-default-footer
                  >
                    <template v-slot:top>
                      <v-toolbar dense flat>
                        <v-toolbar-title>Items</v-toolbar-title>
                        <v-divider class="mx-4" inset vertical></v-divider>
                        <v-spacer></v-spacer>
                        <v-dialog v-model="dialog" max-width="800px">
                          <template v-slot:activator="{ on, attrs }">
                            <v-btn
                              color="yellow"
                              class="mb-2"
                              v-bind="attrs"
                              v-on="on"
                            >
                              New Item
                            </v-btn>
                          </template>
                          <v-card>
                            <v-card-title>
                              <span class="headline">{{ formTitle }}</span>
                            </v-card-title>

                            <v-card-text>
                              <v-container>
                                <v-row>
                                  <v-col cols="12" sm="6" md="4">
                                    <v-expansion-panels>
                                      <v-expansion-panel id="panel" expand>
                                        <v-expansion-panel-header>
                                          <v-row no-gutters>Select item</v-row>
                                        </v-expansion-panel-header>
                                        <v-expansion-panel-content>
                                          <v-select
                                            v-model="editedItem.item"
                                            solo
                                            flat
                                            dense
                                            v-for="(category, i) in categories"
                                            :key="i"
                                            :label="category.name"
                                            :items="category.items"
                                          ></v-select>
                                        </v-expansion-panel-content> </v-expansion-panel
                                    ></v-expansion-panels>
                                  </v-col>
                                  <v-col cols="12" sm="6" md="4">
                                    <v-text-field
                                      v-model="editedItem.brand"
                                      label="Brand"
                                    ></v-text-field>
                                  </v-col>
                                  <v-col cols="12" sm="6" md="4">
                                    <v-text-field
                                      v-model="editedItem.deliverTo"
                                      label="Delivering to"
                                    ></v-text-field>
                                  </v-col>
                                  <v-col cols="12" sm="6" md="4">
                                    <v-text-field
                                      v-model="editedItem.price"
                                      label="Unit price"
                                    ></v-text-field>
                                  </v-col>
                                  <v-col cols="12" sm="6" md="4">
                                    <v-text-field
                                      v-model="editedItem.quanity"
                                      label="Quantity"
                                    ></v-text-field>
                                  </v-col>
                                </v-row>
                              </v-container>
                            </v-card-text>

                            <v-card-actions>
                              <v-spacer></v-spacer>
                              <v-btn color="blue darken-1" text @click="close">
                                Cancel
                              </v-btn>
                              <v-btn color="blue darken-1" text @click="save">
                                Save
                              </v-btn>
                            </v-card-actions>
                          </v-card>
                        </v-dialog>
                        <v-dialog v-model="dialogDelete" max-width="500px">
                          <v-card>
                            <v-card-title>Are you sure you want to delete this item?</v-card-title>
                            <v-card-actions>
                              <v-spacer></v-spacer>
                              <v-btn
                                color="blue darken-1"
                                text
                                @click="closeDelete"
                                >Cancel</v-btn
                              >
                              <v-btn
                                color="blue darken-1"
                                text
                                @click="deleteItemConfirm"
                                >Delete</v-btn
                              >
                              <v-spacer></v-spacer>
                            </v-card-actions>
                          </v-card>
                        </v-dialog>
                      </v-toolbar>
                    </template>
                    <template v-slot:item.actions="{ item }">
                      <v-icon small class="mr-2" @click="editItem(item)">
                        mdi-pencil
                      </v-icon>
                      <v-icon small @click="deleteItem(item)">
                        mdi-delete
                      </v-icon>
                    </template>
                  </v-data-table>

                  <!-- ================================================================== table ends here ==================================================================== -->

                  <v-select
                    label="Membership type"
                    :items="types"
                    v-model="type"
                  ></v-select>

                  <v-select
                    label="Location"
                    :items="districts"
                    v-model="location"
                  ></v-select>

                  <v-text-field
                    label="Mobile number 1"
                    v-model="mobile1"
                  ></v-text-field>

                  <v-text-field
                    label="Mobile number 2"
                    v-model="mobile2"
                  ></v-text-field>

                  <v-text-field
                    label="Home/Work number"
                    v-model="landline"
                  ></v-text-field>

                  <v-btn
                    @click.prevent="createProfile"
                    right
                    class="white--text"
                    color="#36213e"
                    >Create profile</v-btn
                  >
                </v-form>
              </v-container>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-main>
  </v-app>
</template>


<script>
import Axios from "axios";

export default {
  data() {
    return {
      name: "",
      type: "",
      location: "",
      mobile1: "",
      mobile2: "",
      landline: "",
      dataSet: [],

      dialog: false,
      dialogDelete: false,
      headers: [
        {
          text: "Item",
          align: "start",
          sortable: false,
          value: "item",
        },
        { text: "Brand", value: "brand" },
        { text: "Delivering to", value: "deliverTo" },
        { text: "Unit Price", value: "price" },
        { text: "Quantity", value: "quanity" },
        { text: "Actions", value: "actions", sortable: false },
      ],
      desserts: [],
      editedIndex: -1,
      editedItem: {
        item: "",
        brand: 0,
        deliverTo: 0,
        price: 0,
        quanity: 0,
      },
      defaultItem: {
        item: "",
        brand: 0,
        deliverTo: 0,
        price: 0,
        quanity: 0,
      },

      categories: [
        { name: "Cosmetics", items: ["cream", "talc"] },
        { name: "Pharmaceutical", items: ["masks", "gloves"] },
        { name: "Electronic", items: ["mobile phones"] },
        { name: "Automobile", items: [] },
      ],

      types: ["Buyer", "Seller"],

      districts: [
        "Ampara",
        "Anuradhapura",
        "Badulla",
        "Batticaloa",
        "Colombo",
        "Galle",
        "Gampaha",
        "Hambantota",
        "Jaffna",
        "Kalutara",
        "Kandy",
        "Kegalle",
        "Kilinochchi",
        "Kurunegala",
        "Mannar",
        "Matale",
        "Matara",
        "Moneragala",
        "Mullaitivu",
        "Nuwara Eliya",
        "Polonnaruwa",
        "Puttalam",
        "Ratnapura",
        "Trincomalee",
        "Vavuniya",
      ],
    };
  },
  methods: {
    createProfile() {
      Axios.post("/saveperson", {
        name: this.name,
        type: this.type,
        location: this.location,
        mobile1: this.mobile1,
        mobile2: this.mobile2,
        landline: this.landline,
      })
        .then((response) => {
          console.log(response);
          this.$router.replace({ name: "ItemPersonCards" });
        })
        .catch((error) => {
          console.log(error);
        });
    },

    initialize() {
      this.desserts = [
        {
          item: "Your Item",
          brand: 0,
          deliverTo: 0,
          price: 0,
          quanity: 0,
        },
      ];
    },
    editItem(item) {
      this.editedIndex = this.desserts.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      this.editedIndex = this.desserts.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      this.desserts.splice(this.editedIndex, 1);
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    save() {
      if (this.editedIndex > -1) {
        Object.assign(this.desserts[this.editedIndex], this.editedItem);
      } else {
        this.desserts.push(this.editedItem);
      }
      this.close();
    },
  },

  mounted() {
    Axios.get("/categories")
      .then((response) => {
        this.dataSet = response.data;
        console.log(this.dataSet)
      })
      .catch((e) => {
        console.log(e);
      });
  },
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "New Item" : "Edit Item";
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },

  created() {
    this.initialize();
  },
};
</script>


<style scoped>
.inspire {
  background-color: #36213e;
}
.gradiant {
  background: rgb(255, 252, 0);
  background: linear-gradient(
    45deg,
    rgba(255, 252, 0, 1) 0%,
    rgba(54, 33, 62, 1) 48%,
    rgba(255, 0, 121, 1) 100%
  );
}
</style>



